import Vue from 'vue'
import Router from 'vue-router'
import Chat from './views/Chat.vue'
import Join from './views/Join.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'Join',
      component: Join
    },
    {
      path: '/chat',
      name: 'Chat',
      component: Chat
    },
  ]
})
