<template>
  <div id="chat">
    <div class="chat">
      <div class="chat__sidebar">
        <h2>{{ user.roomName }}</h2>
        <div class="chat__sidebar--users">
          <div class="members-container">
            <h4 class="members">Members {{ users.length }}</h4>
          </div>
          <ul class="users">
            <li class="user" v-for="user in users" :key="user.id">{{ user }}</li>
          </ul>
        </div>
      </div>

      <div class="chat__window">
        <ul class="chat__window--messages">
          <li class="chat__window--message" v-for="message in messages" :key="message.message">
            <div class="message--title">
              <h4>{{ message.from }} </h4>
              <span>{{ message.createdAt }}</span>
            </div>
            
            <div class="message--body">
              <p class="message">{{ message.message }}</p>
            </div>
          </li>
          <p><em>{{ typing }}</em></p>
        </ul>
        <div class="chat__window--form">
          <form @submit.prevent="sendMessage" class="message--form">
            <input @keypress="whosTyping" type="text" placeholder="Message" v-model="message">
            <button type="submit">Send</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
import io from 'socket.io-client'
 
const socket = io()

export default {
  name: 'Chat',
  data() {
    return {
      message: '',
      messages: [],
      typing: '',
      users: [],
    }
  },
  computed: {
    user() {
      return this.$store.getters.getUser
    },

    users() {
      return this.$store.getters.getUserList
    }
  },
  methods: {
    sendMessage() {
      socket.emit('sendMessage', { from: this.user.displayName, message: this.message, room: this.user.roomName })
      this.message = ''
    },

    whosTyping() {
      socket.emit('typing', this.user)
    }
  },
  created() {
    socket.emit('join', this.user, err => {
      if (err) console.log(err)
      console.log(this.$store.getters.getUserList)
    })
  },
  mounted() {
    socket.on('updateUserList', users => this.users = users),

    socket.on('newMessage', message => {
      const formattedMessage = {
        from: message.from,
        message: message.message,
        createdAt: moment(message.createdAt).format('h:mm A')
      }
      this.messages = [...this.messages, formattedMessage]
      this.typing = ''
    })

    socket.on('typing', user => this.typing = `${user} is typing...`)
  }
}
</script>

